import React, { useEffect, useState } from 'react';
import Navbar from '../FixedComponet/Navbar';
import PostList from '../Componets/PostList';
import Sidebar from '../FixedComponet/Sidebar';

import { getProfile, homepage } from '../Service/api';

function HomePage() {
  const [posts, setPosts] = useState([]);
  const [profile, setProfile] = useState([]);
  const [searchTerm, setSearchTerm] = useState('');
  const [searchResults, setSearchResults] = useState([]);
  const [showDropdown, setShowDropdown] = useState(false);

  useEffect(() => {
    const fetchPosts = async () => {
      try {
        const username = localStorage.getItem('username');
        const res = await homepage(username);
        const profile = await getProfile(username);
        setProfile(profile);
        setPosts(res);
      } catch (error) {
        console.log('Error loading posts:', error);
      }
    };
    fetchPosts();
  }, []
  );

  return (
    <div>
      {/* Navbar */}
      <Navbar />
      <div style={{ display: 'flex' }}>

        {/* Sidebar Fixed */}
        <div style={{ width: '250px', height: '100vh', position: 'sticky', top: 0, backgroundColor: '#f8f9fa', padding: '10px' }}>
        <Sidebar />
      </div>

        {/* Feed Scroll */}
        <div style={{ flex: 1, padding: '20px' }}>
          <PostList posts={posts} profileId={profile.userId} loggedInUserId={profile.userId} />
        </div>
      </div>
    </div>
  );
};
export default HomePage;
