import React, { useState } from 'react';
import { registerUser } from '../Service/api';
import { useNavigate, Link } from 'react-router-dom';
import exampleImg from '../Logo/Gemini_Generated_Image_p22uhdp22uhdp22u.png'

function RegisterPage() {
  const [username, setUsername] = useState('');
  const [email, setEmail] = useState('');
  const [password, setPassword] = useState('');
  const [errors, setErrors] = useState({ username: '', email: '', password: '', general: '' });
  const navigate = useNavigate();

  const handleRegister = async (e) => {
    e.preventDefault();

    const newErrors = { username: '', email: '', password: '', general: '' };
    let hasError = false;

    if (!username.trim()) {
      newErrors.username = 'Username is required.';
      hasError = true;
    }

    if (!email.trim()) {
      newErrors.email = 'Email is required.';
      hasError = true;
    } else if (!/\S+@\S+\.\S+/.test(email)) {
      newErrors.email = 'Email is invalid.';
      hasError = true;
    }

    if (!password.trim()) {
      newErrors.password = 'Password is required.';
      hasError = true;
    } else if (password.length < 6) {
      newErrors.password = 'Password must be at least 6 characters.';
      hasError = true;
    }

    if (hasError) {
      setErrors(newErrors);
      return;
    }

    try {
      await registerUser({ username, email, password });
      navigate('/');
    } catch (err) {
      newErrors.general = 'Registration failed. Please try again.';
      setErrors(newErrors);
    }
  };

  return (
    <div className="d-flex justify-content-center align-items-center vh-100 bg-dark text-white">
      <div className="row w-100" style={{ maxWidth: '900px' }}>
        {/* Left Side Image */}
        <div className="col-md-6 d-none d-md-block p-0">
          <img
            src={exampleImg}
            alt="Register Visual"
            className="img-fluid h-100"
            style={{ objectFit: 'cover', borderTopLeftRadius: '0.5rem', borderBottomLeftRadius: '0.5rem' }}
          />
        </div>

        {/* Right Side Form */}
        <div className="col-md-6 bg-secondary p-4 rounded-end">
          <h2 className="text-center mb-4">Create Account</h2>

          {errors.general && <div className="text-danger text-center mb-3">{errors.general}</div>}

          <form onSubmit={handleRegister} autoComplete="off">
            <div className="mb-3">
              <label className="form-label text-white">Username</label>
              <input
                autoComplete="off"
                type="text"
                className={`form-control ${errors.username && 'is-invalid'}`}
                value={username}
                onChange={(e) => setUsername(e.target.value)}
              />
              {errors.username && <div className="text-danger">{errors.username}</div>}
            </div>

            <div className="mb-3">
              <label className="form-label text-white">Email address</label>
              <input
                autoComplete="off"
                type="email"
                className={`form-control ${errors.email && 'is-invalid'}`}
                value={email}
                onChange={(e) => setEmail(e.target.value)}
              />
              {errors.email && <div className="text-danger">{errors.email}</div>}
            </div>

            <div className="mb-3">
              <label className="form-label text-white">Password</label>
              <input
                autoComplete="off"
                type="password"
                className={`form-control ${errors.password && 'is-invalid'}`}
                value={password}
                onChange={(e) => setPassword(e.target.value)}
              />
              {errors.password && <div className="text-danger">{errors.password}</div>}
            </div>

            <button type="submit" className="btn btn-light w-100">Register</button>
          </form>

          <div className="mt-3 text-center">
            <small className="text-white">Already have an account? <Link to="/" className="text-light">Login</Link></small>
          </div>
        </div>
      </div>
    </div>
  );
}

export default RegisterPage;
