// export default PostList;
import React from 'react';
import PostCard from './PostCard';

const PostList = ({ posts,profileId,loggedInUserId }) => {
  return (
    <div style={{ display: "flex", flexDirection: "column", gap: "20px", margin: "20px auto", maxWidth: "500px" }}>
      {posts.map((post) => (
        <PostCard key={post.imageUrl} post={post}profileId={profileId} loggedInUserId={loggedInUserId} />
      ))}
    </div>
  );
};

export default PostList;

