import React, { useEffect, useState } from 'react';
import SockJS from 'sockjs-client';
import { Client } from '@stomp/stompjs';
import { useParams } from 'react-router-dom';
import axios from 'axios';
import Sidebar from '../FixedComponet/Sidebar';
let stompClient = null;

const ChatDetailPage = () => {
  const [message, setMessage] = useState('');
  const [chat, setChat] = useState([]);
  const [error, setError] = useState(null); 
  const currentUser = localStorage.getItem("username");
  const { receiver } = useParams();

  useEffect(() => {
    const socket = new SockJS('http://localhost:8080/ws'); 
    stompClient = new Client({
      webSocketFactory: () => socket,
      debug: (str) => console.log(str),
      onConnect: () => {
        console.log('Connected to WebSocket');

       
        stompClient.subscribe('/user/queue/messages', (message) => {
          const received = JSON.parse(message.body);
          setChat((prev) => [...prev, received]);
        });
        setError(null);
      },
      onStompError: (frame) => {
        console.error('STOMP error:', frame);
        setError('Failed to connect to chat service. Please try again later.');
      },
      onWebSocketClose: () => {
        setError('WebSocket connection closed unexpectedly.');
      },
      onWebSocketError: (error) => {
        console.error('WebSocket error:', error);
        setError('An error occurred with the WebSocket connection.');
      },
    });

    stompClient.activate();

    return () => {
      if (stompClient) stompClient.deactivate();
    };
  }, [currentUser]);
  useEffect(() => {
    const fetchChatHistory = async () => {
      try {
        const res = await axios.get(`http://localhost:8080/chat/history/${currentUser}/${receiver}`);
        setChat(res.data);
      } catch (err) {
        console.error("Failed to fetch chat history", err);
      }
    };
  
    if (receiver && currentUser) {
      fetchChatHistory();
    }
  }, [receiver, currentUser]);
  

  const sendMessage = () => {
    if (message.trim() === '' || !stompClient?.connected) return;
  
    const msg = {
      sender: currentUser,
      receiver:receiver,
      content: message,
    };
  
    console.log("Sending message:", msg);
    stompClient.publish({
      destination: '/app/chat',
      body: JSON.stringify(msg),
    });
  
    setChat((prev) => [...prev, msg]);
    setMessage('');
  };
  
  return (
    <div style={{ display: 'flex' }}>
      {/* Sidebar */}
      <div
        style={{
          width: '250px',
          height: '100vh',
          position: 'sticky',
          top: 0,
          backgroundColor: '#f8f9fa',
          padding: '10px',
        }}
      >
        <Sidebar />
      </div>
  
      {/* Main Chat Container */}
      <div className="container mt-4" style={{ flex: 1, marginLeft: "30px" }}>
        {/* Header */}
        <div
          className="d-flex align-items-center p-3 mb-3 rounded "
          style={{
            backgroundColor: '#fffff',
            border: '1px solid #dee2e6',
            marginLeft:"30px",
            marginRight:"30px"
          }}
        >
          <img
            src={`http://localhost:8080/profilePic/${receiver}.jpg`}
            alt="Profile"
            width={50}
            height={50}
            className="rounded-circle"
          />
          <div className="ms-3">
            <h5 className="mb-0">@{receiver}</h5>
            <small className="text-muted">Active now</small>
          </div>
        </div>
  
        {/* Error Message */}
        {error && (
          <div className="alert alert-danger" role="alert">
            {error}
          </div>
        )}
  
        {/* Chat Box */}
        <div
          className="p-3 mt-2 rounded"
          style={{
            height: 400,
            overflowY: 'auto',
            borderColor:'black',
            backgroundColor: 'rgb(243 243 243)',
            marginLeft: "30px",
            marginRight: "30px",
          }}
        >
          {chat.map((msg, idx) => (
            <div
              key={idx}
              className={`d-flex mb-2 ${msg.sender === currentUser ? 'justify-content-end' : 'justify-content-start'}`}
            >
              <div
                className={`p-2 rounded px-3`}
                style={{
                  maxWidth: '70%',
                  backgroundColor: msg.sender === currentUser ? '#d1e7dd' : '#ffffff',
                  fontSize: '15px',
                  fontWeight: 500,
                }}
              >
                {msg.content}
              </div>
            </div>
          ))}
        </div>
  
        {/* Message Input */}
        <div className="d-flex align-items-center p-2 bg-white rounded shadow-sm mt-2"  style={{ marginLeft: "30px", marginRight: "30px" }}>
          <input
            type="text"
            value={message}
            onChange={(e) => setMessage(e.target.value)}
            className="form-control me-2"
            placeholder="Type a message..."
            style={{ fontSize: '15px' }}
          />
          <button className="btn btn-success" onClick={sendMessage}>
            Send
          </button>
        </div>
      </div>
    </div>
  );
}  

export default ChatDetailPage;
