import { useNavigate } from "react-router-dom";
import { Button } from "react-bootstrap";
const UserCard = ({ user, onConfirm, onFollowBack, type ,onCancel}) => {
  const navigate = useNavigate();

  const renderActionButton = () => {
    if (type === "follow-request") {
      return (
        
        <><button className="btn btn-success" onClick={() => onConfirm(user.userId)}>
          Confirm
        </button>
        <button className="btn btn-danger" onClick={() => onCancel(user.userId)}>
        Cancel
      </button>
      </>
      );
    }
    if (type === "search") {
      return null;
    }
    if (type === "message") 
        return(
        <Button
                variant="success"
                onClick={() => navigate(`/chatDetailPage/${user.username}`)}>
                Message
              </Button>
              );
    
    
    if (type === "follow-back") {
      return (
        <button className="btn btn-warning" onClick={() => onFollowBack(user.userId)}>
          Follow Back
        </button>
      );
    }
    return null;
  };

  return (
    <div className="card p-3 mb-2 d-flex justify-content-between flex-row align-items-center">
      <div className="d-flex gap-3 align-items-center">
        <img
          src={`http://localhost:8080${user.profilePic}`}
          width={50}
          height={50}
          className="rounded-circle"
          alt="Profile"
        />
        <b>@{user.username}</b>
      </div>

      <div className="d-flex gap-2">
        {renderActionButton()}
        <button
          className="btn btn-primary"
          onClick={() =>
            navigate(`/profile/${user.username}`, {
              state: { userId: user.userId },
            })
          }
        >
          View Profile
        </button>
      </div>
    </div>
  );
};

export default UserCard;
