import React, { useState } from 'react';
import { Card } from "react-bootstrap";
import { useLocation, useNavigate } from 'react-router-dom';
import axios from 'axios';
import ImageUploader from '../Componets/ImageUploader';

function EditPage() {
  const location = useLocation();
  const navigate = useNavigate();
  const profile = location.state?.profile;

  const [name, setName] = useState(profile?.name || '');
  const [username, setUsername] = useState(profile?.username || '');
  const [bio, setBio] = useState(profile?.bio || '');
  const [isPublic, setIsPublic] = useState(profile?.isPublic || true);
  const [profilePicFile, setProfilePicFile] = useState(null);
  const [previewImage, setPreviewImage] = useState(`http://localhost:8080${profile.profilePic}`);
  const [showCropper, setShowCropper] = useState(false);

  const handleCroppedImage = (croppedBlob) => {
    const file = new File([croppedBlob], 'cropped.jpg', { type: 'image/jpeg' });
    setProfilePicFile(file);
    setPreviewImage(URL.createObjectURL(croppedBlob));
    setShowCropper(false);
  };

  const handleSubmit = async (e) => {
    e.preventDefault();

    const formData = new FormData();
    formData.append("username", username);
    formData.append("bio", bio);
    formData.append("isPublic", isPublic);
    formData.append("name", name);
    if (profilePicFile) {
      formData.append("profilePic", profilePicFile);
    }

    try {
      await axios.put(
        `http://localhost:8080/profile/profile/edit/${profile.userId}`,
        formData,
        { headers: { "Content-Type": "multipart/form-data" } }
      );
      alert("Profile updated!");
      navigate(`/profile/${username}`); // Redirect to profile page
    } catch (err) {
      console.error("Error updating profile", err);
      alert("Failed to update profile");
    }
  };

  if (!profile) return <p>No profile data found.</p>;

  return (
    <div style={{ display: 'flex', justifyContent: 'center' }}>
      <Card className="p-4 shadow rounded-4 mt-4" style={{ maxWidth: '600px', width: '100%' }}>
        <h3>Edit Profile</h3>
        <form onSubmit={handleSubmit}>
          <div style={{ textAlign: 'center', marginBottom: '20px' }}>
            <img
              src={previewImage}
              alt="Profile"
              style={{
                width: '150px',
                height: '150px',
                borderRadius: '50%',
                objectFit: 'cover',
                border: '3px solid black'
              }}
            />
            <div className="mt-2">
              <input
                type="file"
                className="form-control"
                accept="image/*"
                onChange={(e) => {
                  const file = e.target.files[0];
                  if (file) {
                    const imageUrl = URL.createObjectURL(file);
                    setShowCropper({ file, imageUrl });
                  }
                }}
              />
            </div>
          </div>

          {showCropper && (
            <ImageUploader
              file={showCropper.file}
              imageUrl={showCropper.imageUrl}
              onComplete={handleCroppedImage}
              onCancel={() => setShowCropper(false)}
            />
          )}

          <div className="mb-3">
            <label>Name </label>
            <input
              type="text"
              className="form-control"
              value={name}
              onChange={(e) => setName(e.target.value)}
            />
          </div>

          <div className="mb-3">
            <label>Username</label>
            <input
              type="text"
              className="form-control"
              value={username}
              onChange={(e) => setUsername(e.target.value)}
              required
            />
          </div>

          <div className="mb-3">
            <label>Bio</label>
            <input
              type="text"
              className="form-control"
              value={bio}
              onChange={(e) => setBio(e.target.value)}
            />
          </div>

          <div className="form-check form-switch mb-3">
            <input
              className="form-check-input"
              type="checkbox"
              role="switch"
              id="switchCheckDefault"
              checked={isPublic}
              onChange={(e) => setIsPublic(e.target.checked)}
            />
            <label className="form-check-label" htmlFor="switchCheckDefault">
              Public Account
            </label>
          </div>

          <button type="submit" className="btn btn-primary">Save Changes</button>
        </form>
      </Card>
    </div>
  );
}

export default EditPage;
