import React, { useState, useEffect } from 'react';
import { Card, Form, Button } from 'react-bootstrap';
import { FaHeart, FaRegComment, FaShare, FaRegHeart } from 'react-icons/fa';
import axios from 'axios';

const PostCard = ({ post, loggedInUserId }) => {
  const [showComments, setShowComments] = useState(false);
  const [comments, setComments] = useState([]);
  const [newComment, setNewComment] = useState('');
  const [isLiked, setIsLiked] = useState(false);
  const [likeCount, setLikeCount] = useState(post.likeCount)
  const [likedUsers, setLikedUsers] = useState([]);
  const loggedInUsername = localStorage.getItem('username');

  const fetchComments = async () => {
    try {
      const res = await axios.get(`http://localhost:8080/profile/comment/${post.postId}`);
      setComments(res.data);

    } catch (err) {
      console.error("Failed to fetch comments", err);
    }
  };
  

  const handleCommentSubmit = async () => {
    if (!newComment.trim()) return;
    try {
      await axios.post(
        `http://localhost:8080/profile/comment`,
        newComment,
        {
          params: { postId: post.postId, userId: loggedInUserId },
          headers: { 'Content-Type': 'text/plain' },
        }
      );
      setNewComment('');
      fetchComments();
    } catch (err) {
      console.error("Failed to post comment", err);
    }
  };
  const handleDeleteComment = async (commentId) => {
    if (window.confirm("Are you sure you want to delete this comment?")) {
      try {
        await axios.delete(`http://localhost:8080/profile/DeleteComment/${commentId}`, {
          params: { userId: loggedInUserId },
        });
        fetchComments(); // refresh comments
      } catch (error) {
        console.error(error);
        alert("Failed to delete comment");
      }
    }
  };
  const handleLike = async () => {
    try {
      // Instant UI update
      setIsLiked(prev => !prev);
      setLikeCount(prev => (isLiked ? prev - 1 : prev + 1));

      // Backend update
      await axios.post(`http://localhost:8080/profile/like/${post.postId}`, null, {
        params: { userId: loggedInUserId },
      });

    } catch (error) {
      console.error("Error toggling like", error);
    }
  };





  const fetchLikes = async () => {
    try {
      const res = await axios.get(`http://localhost:8080/profile/likes/${post.postId}`);
      setLikedUsers(res.data || []);
      setLikeCount(res.data.length || 0);

      // Check if current user liked
      const liked = res.data.some(username => username === loggedInUsername);
      setIsLiked(liked);

    } catch (error) {
      console.error("Error fetching likes", error);
    }
  };

  useEffect(() => {
    fetchLikes();
  }, []);

  const renderLikedText = () => {
    if (likedUsers.length === 0) return "Be the first to like this!";
    if (likedUsers.length === 1) return `Liked by ${likedUsers[0]}`;
    if (likedUsers.length === 2) return `Liked by ${likedUsers[0]} and ${likedUsers[1]}`;
    return `Liked by ${likedUsers[0]}, ${likedUsers[1]} and ${likedUsers.length - 2} others`;
  };

  const fetchIsLiked = async () => {
    try {
      const res = await axios.get(`http://localhost:8080/profile/profile/isLiked/${post.postId}`, {
        params: { userId: loggedInUserId },
      });
      setIsLiked(res.data.liked);
    } catch (error) {
      console.error("Error checking like status", error);
    }
  };

  useEffect(() => {
    fetchIsLiked();
  }, []);



  useEffect(() => {
    if (showComments) {
      fetchComments();
    }
    fetchLikes();
    // fetchLiked
  }, [showComments]);

  return (
    <Card className="mb-4 shadow-sm rounded-4" style={{ maxWidth: "500px" }}>
      <Card.Header className="d-flex align-items-center gap-2">
  <img
    src={`http://localhost:8080${post.profilePic}`}
    alt="profile"
    width="40"
    height="40"
    className="rounded-circle"
  />
  
  <div className="d-flex flex-column">
    <b>@{post.ownerUsername}</b>
    {post.location && (
      <span className="text-muted" style={{ fontSize: "13px" }}>
        {post.location}
      </span>
    )}
  </div>
</Card.Header>
      {!showComments && post.imageUrl && (
        <Card.Img
          variant="top"
          src={`http://localhost:8080${post.imageUrl}`}
          style={{ objectFit: 'cover' }}
        />
      )}

      <Card.Body>
        <div className="d-flex gap-3 fs-4 mb-2">
          <div className="d-flex align-items-center justify-content-center flex-column">
            {isLiked ? (
              <FaHeart
                onClick={handleLike}
                style={{ cursor: "pointer", color: "red" }}

              />
            ) : (
              <FaRegHeart
                onClick={handleLike}
                style={{ cursor: "pointer", fillcolor: "blck" }}
              />
            )}
            {/* Liked by username text */}
            {/* <p>{likeCount || 0}</p> */}
          </div>
          <div className="d-flex align-items-center gap-1" style={{ }}>
            <FaRegComment
              style={{ cursor: "pointer" }}
              onClick={() => setShowComments(!showComments)}
            />
           <p style={{ margin: 0, fontSize: "18px" }}>{post.commentCount || 0}</p>
          </div>

        </div>
        <p>{post.content}</p>

        <div style={{ fontSize: "14px", marginTop: "4px", color: "#555" }}>
          {renderLikedText()}
        </div>
        

        <small className="text-muted">
          {post.createdAt ? new Date(post.createdAt).toLocaleString() : ''}
        </small>

        {/* Comment Section */}
        {showComments && (
          <div className="mt-3">
            <h6 className="fw-bold">Comments</h6>
            <div style={{ maxHeight: '200px', overflowY: 'auto' }}>
              {comments.map((comment, idx) => (
                <div key={idx} className="mb-2 border-bottom pb-1 d-flex align-items-center justify-content-between">
                  {/* Left side: profile pic + comment */}
                  <div className="d-flex align-items-center gap-2">
                    <img
                      src={`http://localhost:8080${comment.profilePic}`}
                      alt="profile"
                      width="30"
                      height="30"
                      className="rounded-circle"
                    />
                    <div>
                      <b>@{comment.userName}</b> {comment.comment}
                    </div>
                  </div>
                  {/* Right side: delete button */}
                  {(loggedInUserId === comment.userId || loggedInUserId === post.ownerId) && (
                    <button
                      className="btn btn-sm btn-outline-danger"
                      onClick={() => handleDeleteComment(comment.commentId)}
                    >
                      x
                    </button>
                  )}
                </div>
              ))}
            </div>
            <Form.Group className="mt-2 d-flex">
              <Form.Control
                type="text"
                placeholder="Write a comment..."
                value={newComment}
                onChange={(e) => setNewComment(e.target.value)}
              />
              <Button variant="primary" onClick={handleCommentSubmit} className="ms-2">
                Post
              </Button>
            </Form.Group>
          </div>
        )}
      </Card.Body>
    </Card>
  );
}

export default PostCard;
