import React, { useState, useEffect } from 'react';
import { useNavigate, Link } from 'react-router-dom';
import { loginUser } from '../Service/api';
import exampleImg from '../Logo/Gemini_Generated_Image_p22uhdp22uhdp22u.png'

function LoginPage() {
  const [username, setUsername] = useState('');
  const [password, setPassword] = useState('');
  const [errors, setErrors] = useState({ username: '', password: '', general: '' });

  const navigate = useNavigate();

  const handleLogin = async (e) => {
    e.preventDefault();

    const newErrors = { username: '', password: '', general: '' };
    let hasError = false;

    if (!username.trim()) {
      newErrors.username = "Username is required.";
      hasError = true;
    }

    if (!password.trim()) {
      newErrors.password = "Password is required.";
      hasError = true;
    }

    if (hasError) {
      setErrors(newErrors);
      return;
    }

    try {
      const token = await loginUser({ username, password });
      localStorage.setItem("token", token);
      localStorage.setItem("username", username);
      navigate("/homepage");
    } catch (error) {
      setErrors({ ...newErrors, general: "Invalid username or password." });
    }
  };

  useEffect(() => {
    setUsername('');
    setPassword('');
  }, []);

  return (
    <div className="d-flex justify-content-center align-items-center vh-100 bg-dark text-white">
      <div className="row w-100" style={{ maxWidth: '900px' }}>
        
        {/* Left Side Image */}
        <div className="col-md-6 d-none d-md-block p-0">
          <img
            src={exampleImg}
            alt="Login Visual"
            className="img-fluid h-100"
            style={{ objectFit: 'cover', borderTopLeftRadius: '0.5rem', borderBottomLeftRadius: '0.5rem' }}
          />
        </div>

        {/* Right Side Form */}
        <div className="col-md-6 bg-secondary p-4 rounded-end">
          <h2 className="text-center fw-bold mb-4">Welcome Back!</h2>

          {errors.general && <div className="text-danger text-center mb-3">{errors.general}</div>}

          <form onSubmit={handleLogin} autoComplete="off">
            <div className="mb-3">
              <label htmlFor="username" className="form-label text-white">Username</label>
              <input
                autoComplete="off"
                type="text"
                className={`form-control ${errors.username && 'is-invalid'}`}
                id="username"
                value={username}
                onChange={(e) => setUsername(e.target.value)}
              />
              {errors.username && <div className="text-danger">{errors.username}</div>}
            </div>

            <div className="mb-3">
              <label htmlFor="password" className="form-label text-white">Password</label>
              <input
                autoComplete="new-password"
                type="password"
                className={`form-control ${errors.password && 'is-invalid'}`}
                id="password"
                value={password}
                onChange={(e) => setPassword(e.target.value)}
              />
              {errors.password && <div className="text-danger">{errors.password}</div>}
            </div>

            <div className="d-flex justify-content-between mb-3">
              <a href="#" className="text-decoration-none text-danger">Forgot Password?</a>
              <Link to="/register" className="text-decoration-none text-light">New User? Register</Link>
            </div>

            <button type="submit" className="btn btn-light w-100">Login</button>
          </form>
        </div>
      </div>
    </div>
  );
}

export default LoginPage;
