import React, { useState, useRef, useEffect } from 'react';
import axios from 'axios';
import Sidebar from '../FixedComponet/Sidebar';
import LocationInput from '../Componets/LocationInput';
import Cropper from 'react-easy-crop';
import getCroppedImg from '../Helper/getCroppedImg';
import ConfirmationDialog from '../Componets/ConfirmationDialog';
import { useNavigate } from 'react-router-dom';


const Create = () => {
  const username = localStorage.getItem('username') || '';
  const [file, setFile] = useState(null);
  const [content, setContent] = useState('');
  const [location, setLocation] = useState('');
  const [imageUrl, setImageUrl] = useState('');
  const [crop, setCrop] = useState({ x: 0, y: 0 });
  const [zoom, setZoom] = useState(1);
  const [croppedAreaPixels, setCroppedAreaPixels] = useState(null);
  const [uploadStatus, setUploadStatus] = useState('');
  const [isLocationEnabled, setIsLocationEnabled] = useState(true);
  const [isImageSelected, setIsImageSelected] = useState(false);
  const[showConfirm ,setShowConfirm]=useState(false);
  const locationInput = useRef(null);
  const navigate = useNavigate();

  useEffect(() => {
    if (!window.google || !locationInput.current) return;
    const autocomplete = new window.google.maps.places.Autocomplete(locationInput.current, {
      types: ['geocode'],
    });

    autocomplete.addListener('place_changed', () => {
      const place = autocomplete.getPlace();
      setLocation(place.formatted_address || '');
    });
  }, []);

  
  const handleUpload = async (e) => {
    e.preventDefault();
    setShowConfirm(true);
    if (!file || !content) {
      setUploadStatus('All fields are required.');
      return;
    }
  };
  
  const confirmAndUpload = async () => {
    setShowConfirm(false);
  
    try {
      const croppedBlob = await getCroppedImg(imageUrl, croppedAreaPixels);
  
      const formData = new FormData();
      formData.append('file', croppedBlob, 'cropped.jpg');
      formData.append('username', username);
      formData.append('content', content);
      formData.append('location', location);
  
      const response = await axios.post('http://localhost:8080/profile/upload', formData, {
        headers: { 'Content-Type': 'multipart/form-data' }
      });
  
      setUploadStatus(`Upload successful: ${response.data}`);
  
      // Clear form
      setFile(null);
      setImageUrl('');
      setContent('');
      setLocation('');
      setZoom(1);
      setCrop({ x: 0, y: 0 });
  
      //  Redirect to profile after successful upload
      navigate(`/profile/${username}`);
  
    } catch (error) {
      console.error('Upload failed:', error);
      setUploadStatus('Upload failed.');
    }
  };
  
  
  const resetForm = () => {
    setFile(null);
    setImageUrl('');
    setContent('');
    setLocation('');
    setZoom(1);
    setCrop({ x: 0, y: 0 });
    setIsImageSelected(false);
  };

  return (
    <div className="container-fluid">
      <div className="row">
        {/* Sidebar */}
        <div style={{ width: '250px', height: '100vh', position: 'sticky', top: 0, backgroundColor: '#f8f9fa', padding: '10px' }}>
          <Sidebar />
        </div>

        {/* Form Section */}
        <div className="col-md-9 p-5">
          <div className="card shadow p-4">
            <h3 className="mb-4">Upload a New Post</h3>
            <form onSubmit={handleUpload}>
              {/* Content */}
              <div className="mb-3">
                <label className="form-label">Caption (Optional)</label>
                <textarea
                  className="form-control"
                  rows="3"
                  value={content}
                  onChange={(e) => setContent(e.target.value)}
                  placeholder="Write a caption..."
                ></textarea>
              </div>

              {/* Location Switch */}
              <div className="mb-3">
                <label className="form-label">Location</label>
                <div className="form-check form-switch">
                  <input
                    className="form-check-input"
                    type="checkbox"
                    checked={isLocationEnabled}
                    onChange={() => setIsLocationEnabled(!isLocationEnabled)}
                  />
                </div>
              </div>

              {/* Location Input */}
              {isLocationEnabled && (
                <LocationInput location={location} setLocation={setLocation} locationInputRef={locationInput} />
              )}

              {/* Image Upload */}
              <div className="mb-3">
                <label className="form-label">Select Image <span className="text-danger">*</span></label>
                <input
                  type="file"
                  className="form-control"
                  accept="image/*"
                  onChange={(e) => {
                    const selected = e.target.files[0];
                    if (selected) {
                      setIsImageSelected(true);
                      setFile(selected);
                      setImageUrl(URL.createObjectURL(selected));
                    }
                  }}
                  required
                />
              </div>

              {/* Cropper */}
              {imageUrl && (
                <div style={{ position: 'relative', width: '100%', height: 300 }} className="mb-3">
                  <Cropper
                    image={imageUrl}
                    crop={crop}
                    zoom={zoom}
                    aspect={4 / 3}
                    onCropChange={setCrop}
                    onZoomChange={setZoom}
                    onCropComplete={(_, croppedAreaPixels) => setCroppedAreaPixels(croppedAreaPixels)}
                  />
                </div>
              )}

              {/* Clear Button */}
              {isImageSelected && (
                <button
                  type="button"
                  className="btn btn-danger mt-2"
                  onClick={resetForm}
                >
                   Clear All
                </button>
              )}

              {/* Submit */}
              <button type="submit" className="btn btn-primary mt-3 ms-2"> Upload</button>
            </form>

            {/* Status */}
            {uploadStatus && (
              <div className="alert alert-info mt-3">{uploadStatus}</div>
            )}
            <ConfirmationDialog
              show={showConfirm}
              title="Confirm Upload"
              message="Are you sure you want to post this?"
              onConfirm={confirmAndUpload}
              onCancel={() => setShowConfirm(false)}
              confirmLabel="Yes, Post"
              cancelLabel="Cancel"
            />
          </div>
        </div>
      </div>
    </div>
  );
};

export default Create;
