import React, { useEffect, useState } from 'react';
import { useParams, useNavigate } from 'react-router-dom';
import { getProfile, getFollowers, getFollowing, followUser, unfollowUser } from '../Service/api';
import { Card } from "react-bootstrap";
import ProfileCard from '../Componets/ProfileCard';
import { Modal } from 'react-bootstrap';
import PostCard from '../Componets/PostCard';
import axios from 'axios';
import { useLocation } from 'react-router-dom';
import { toast } from 'react-toastify';
import 'react-toastify/dist/ReactToastify.css';
import ConfirmationDialog from '../Componets/ConfirmationDialog';
import Sidebar from '../FixedComponet/Sidebar';


const ProfilePage = () => {
  const location = useLocation();
  const { userId: targetUserIdFromState } = location.state || {};
  const [loggedInIdProfile, setLoggedInIdProfile] = useState(null);
  const [viewedUserId, setViewedUserId] = useState(targetUserIdFromState || null);
  const [canViewPosts, setCanViewPosts] = useState(false);
  const { username } = useParams();
  const loggedInUsername = localStorage.getItem('username');
  const [profile, setProfile] = useState({});
  const [posts, setPosts] = useState([]);
  const [followers, setFollowers] = useState([]);
  const [following, setFollowing] = useState([]);
  const [isFollowing, setIsFollowing] = useState(false);
  const [showModal, setShowModal] = useState(false);
  const [selectedPost, setSelectedPost] = useState(null);
  const [followRequests, setFollowRequests] = useState(null);
  const [isFollowBack, setIsFollowBack] = useState(null);
  const [showDeleteModal, setShowDeleteModal] = useState(false);
  const [postToDelete, setPostToDelete] = useState(null);
  const [showDeleteDialog, setShowDeleteDialog] = useState(false);

    const fetchProfileData = async () => {
      try {
        const loggedInProfile = await getProfile(loggedInUsername);
        setLoggedInIdProfile(loggedInProfile);

        const profileData = await getProfile(username || loggedInUsername);
        setProfile(profileData);

        const folRes = await getFollowers(profileData.userId);
        setFollowers(folRes);

        const wingRes = await getFollowing(profileData.userId);
        setFollowing(wingRes);

        // use local variables here instead of state (they are guaranteed to be available)
        if (profileData.userId !== loggedInProfile.userId) {
          const res = await axios.get(
            `http://localhost:8080/profile/isProfileVisible?followerId=${loggedInProfile.userId}&followingId=${profileData.userId}`
          );
          setIsFollowing(res.data);   // this should reflect accept = true
          setCanViewPosts(res.data);
          const isBack = await axios.get(
            `http://localhost:8080/profile/isFollow/${profileData.userId}/${loggedInProfile.userId}`
          );
          setIsFollowBack(isBack.data);
        } else {
          setCanViewPosts(true);
        }
      } catch (err) {
        console.error(err);
      }
    };
  useEffect(() => {
    fetchProfileData();
  }, [username]);



  const handleFollowToggle = async () => {
    try {
      if (isFollowing) {
        await axios.put(`http://localhost:8080/profile/unfollow/${loggedInIdProfile.userId}/${profile.userId}`);
      } else {
        await axios.post(`http://localhost:8080/profile/sendFollowRequest/${loggedInIdProfile.userId}/${profile.userId}`);
      }
      setIsFollowing(!isFollowing);
    } catch (err) {
      console.error(err);
    }
  };

  const handleDelete = (postId) => {
    setPostToDelete(postId);         
    setShowDeleteModal(true);        
  };
  const confirmDelete = async () => {
    try {
      await axios.delete(`http://localhost:8080/profile/deletePosts/${postToDelete}`, {
        params: { username: localStorage.getItem('username') }
      });
      setPosts(prevPosts => prevPosts.filter(post => post.postId !== postToDelete));

      toast.success("Post deleted successfully!");
      fetchProfileData();
    } 
    catch (error) {
      console.error('Failed to delete post:', error);
      toast.error("Failed to delete post.");
    } finally {
      setShowDeleteModal(false);  // close the delete modal
      setPostToDelete(null);      // clear the postId
      setShowModal(false);        // also close the Post details modal
    }
  };


  const handleCancelFollow = async (userId) => {
    try {
      await axios.delete(`http://localhost:8080/profile/cancel/${loggedInIdProfile.userId}/${userId}`);
      setFollowRequests(prev => prev.filter(u => u.userId !== userId));
    } catch (err) {
      console.error("Failed to cancel follow request", err);
    }
  };


  /////View profile////// 
  const handleClick = async (postId) => {
    try {
      const res = await axios.get(`http://localhost:8080/profile/getPost/${postId}`);
      const post = res.data;
      setSelectedPost(post);
      setShowModal(true);
    } catch (error) {
      console.error('Error fetching post:', error);
    }
  };
  return (
    <div style={{ display: 'flex', minHeight: '100vh' }}>
      {/* Sidebar Section */}
      <div style={{ width: '250px', height: '100vh', position: 'sticky', top: 0, backgroundColor: '#f8f9fa', padding: '10px' }}>
        <Sidebar />
      </div>
  
      {/* Main Content Section */}
      <div style={{ flex: 1, padding: '20px', maxWidth: '1000px', margin: '0 auto' }}>
        <Card className="p-4 shadow rounded-4 mb-3">
          <ProfileCard
            profile={profile}
            isOwnProfile={username === loggedInUsername || !username}
            isFollowing={isFollowing}
            isFollowBack={isFollowBack}
            handleFollowToggle={handleFollowToggle}
            followersCount={followers.length}
            followingCount={following.length}
            postsCount={profile?.postImages?.length || 0}
          />
        </Card>
        {/* Post Section */}
        {canViewPosts ? (
          <Card className="p-4 shadow rounded-4 mb-3">
            <h4 className="mb-3">Posts</h4>
            <div style={{ display: 'flex', flexWrap: 'wrap', gap: '1px' }}>
              {profile.postImages?.map((imgPath, index) => (
                <img
                  key={index}
                  src={`http://localhost:8080${imgPath.imageUrl}`}
                  alt="Post"
                  style={{
                    width: '300px',
                    height: '300px',
                    objectFit: 'cover',
                    border: '1px solid #ccc',
                    cursor: 'pointer'
                  }}
                  onClick={() => handleClick(imgPath.postId)}
                />
              ))}
            </div>
          </Card>
        ) : (
          <div className="text-center mt-3">
            <i>This account is private. Follow to see posts.</i>
          </div>
        )}
  
        {/* Post Modal */}
        <Modal show={showModal} onHide={() => setShowModal(false)} centered size="lg">
          <Modal.Header closeButton>
            <Modal.Title>Post Details</Modal.Title>
            {selectedPost?.ownerUsername === loggedInUsername && (
              <button
                className="btn btn-sm btn-danger ms-auto"
                onClick={() => {
                  setPostToDelete(selectedPost.postId);
                  setShowDeleteDialog(true);
                }}
              >
                 Delete
              </button>
            )}
          </Modal.Header>
          <Modal.Body className="p-0">
            {selectedPost && <PostCard post={selectedPost} loggedInUserId={profile.userId} />}
          </Modal.Body>
        </Modal>
  
        {/* Delete Confirmation Dialog */}
        <ConfirmationDialog
          show={showDeleteDialog}
          title="Delete Post"
          message="Are you sure you want to delete this post?"
          confirmLabel="Delete"
          cancelLabel="Cancel"
          onCancel={() => {
            setShowDeleteDialog(false);
            setPostToDelete(null);
          }}
          onConfirm={confirmDelete}
        />
      </div>
    </div>
  );
  
};

export default ProfilePage;
