import axios from 'axios';

const URL = "http://localhost:8080/auth"; 
const BaseURL = "http://localhost:8080/home"; 
export const registerUser = async (data) => {
    try {
        const response = await axios.post(`${URL}/register`, data);
        return response.data;
    } catch (error) {
        console.log("Register API Error:", error);
        throw error;
    }
}

export const loginUser = async (data) => {
    try {
        const response = await axios.post(`${URL}/login`, data);
        return response.data;
    } catch (error) {
        console.log("Login API Error:", error);
        throw error;
    }
}

export const homepage = async (username) => {
    try {
        const response = await axios.get(`${BaseURL}/${username}`);
        return response.data;
    } catch (error) {
        console.log("Home page API Error:", error);
        throw error;
    }
}

export const getProfile = async (username) => {
    const res = await axios.get(`http://localhost:8080/profile/${username}`);
    return res.data;
  };
  export const getProfileById = async (userId) => {
    const res = await axios.get(`http://localhost:8080/getProfileById/userId/${userId}`);
    return res.data;
  };
  
  export const getFollowers = async (userId) => {
    const res = await axios.get(`http://localhost:8080/profile/followers/${userId}`);
    return res.data;
  };
  
  export const getFollowing = async (userId) => {
    const res = await axios.get(`http://localhost:8080/profile/following/${userId}`);
    return res.data;
  };
  export const followUser= async (follower,following) => {
    const res = await axios.post(`http://localhost:8080/profile/${follower}/${following}`);
    return res.data;
  };
  export const unfollowUser= async (follower,following) => {
    const res = await axios.put(`http://localhost:8080/profile/${follower}/${following}`);
    return res.data;
};
export const accepetRequest= async (follower,following,accept) => {
    const res = await axios.get(`http://localhost:8080/profile/${follower}/${following}/${accept}`);
    return res.data;
};



  