import { useState, useEffect } from "react";
import axios from "axios";
import UserCard from "../FixedComponet/UserCard";
import Sidebar from "../FixedComponet/Sidebar";
const Search = () => {
  const [searchTerm, setSearchTerm] = useState('');
  const [searchResults, setSearchResults] = useState([]);
  const [showDropdown, setShowDropdown] = useState(false);

  // Live search on typing
  useEffect(() => {
    
    const delayDebounceFn = setTimeout(() => {
      if (searchTerm.trim() !== '') {
        axios.get(`http://localhost:8080/home/search?username=${searchTerm}`)
          .then(res => {
            setSearchResults(res.data);
            console.log(searchResults)
            setShowDropdown(true);
          })
          .catch(err => {
            console.error("Search failed", err);
            setShowDropdown(false);
          });
      } else {
        setSearchResults([]);
        setShowDropdown(false);
      }
    }, 0.3); // debounce by 300ms

    return () => clearTimeout(delayDebounceFn);
  }, [searchTerm]);

  const handleSearch = (e) => {
    e.preventDefault(); // no need to reload
  };

  return (
    <div style={{ display: 'flex' }}>
      {/* Sidebar */}
      <div style={{ width: '250px', height: '100vh', position: 'sticky', top: 0, backgroundColor: '#f8f9fa', padding: '10px' }}>
        <Sidebar />
      </div>
      <div style={{ position: 'relative', width: '50%', margin: 'auto', marginTop: '20px' }}>
        <form className="d-flex" role="search" onSubmit={handleSearch}>
          <input
            className="form-control me-2"
            placeholder="Search by username"
            aria-label="Search"
            value={searchTerm}
            onChange={(e) => setSearchTerm(e.target.value)}
            onFocus={() => { if (searchResults.length) setShowDropdown(true); }}
            onBlur={() => setTimeout(() => setShowDropdown(false), 200)} // wait to allow click on UserCard
          />
          <button className="btn btn-outline-success" type="submit">Search</button>
        </form>

        {showDropdown && searchResults.length > 0 && (
          <div style={{
            position: 'absolute',
            backgroundColor: '#fff',
            zIndex: 1000,
            border: '1px solid #ccc',
            marginTop: '5px',
            borderRadius: '5px',
            width: '100%',
            maxHeight: '300px',
            overflowY: 'auto',
            boxShadow: '0 4px 8px rgba(0,0,0,0.1)'
          }}>
            {searchResults.map(user => (
              <UserCard key={user.userId} user={user} type="search" />
            ))}
          </div>
        )}
      </div>
    </div>
  );
};

export default Search;
