import { useNavigate, useParams } from "react-router-dom";
import { Button } from "react-bootstrap";
import axios from "axios";
import React, { useEffect, useState } from 'react';
import { getFollowers, getFollowing, getProfile } from "../Service/api";

const ProfileCard = ({ profile, isOwnProfile, isFollowing, isFollowBack, handleFollowToggle, followersCount, followingCount, postsCount }) => {
  const navigate = useNavigate();
  const { username } = useParams();
  const loggedInUsername = localStorage.getItem('username');

  const [followers, setFollowers] = useState([]);
  const [following, setFollowing] = useState([]);
  const [followStatus, setFollowStatus] = useState({
    isFollowing: isFollowing,
    isFollowBack: isFollowBack,
  });
  
  useEffect(() => {
    const fetchFollower = async () => {
      try {
        const currentUsername = username || loggedInUsername;
        const currntProfile = await getProfile(currentUsername);
        const folRes = await getFollowers(currntProfile.userId);
        setFollowers(folRes);

        const wingRes = await getFollowing(currntProfile.userId);
        setFollowing(wingRes);

      } catch (err) {
        console.error(err);
      }
    };

    fetchFollower();
  }, [username, navigate]);


  const handleEditPage = async (profile) => {

  };
 

  return (
    <div className="d-flex align-items-center gap-4" style={{borderTopLeftRadius: '50%',
    borderTopRightRadius: '50%',}}>
      <img
        src={`http://localhost:8080${profile.profilePic}`}
        alt="Profile"
        style={{ width: '120px', height: '120px', borderRadius: '50%', objectFit: 'cover' }}/>
      <div>
        <h2>@{profile.username}</h2>
        <p2>{profile.name}</p2>
        <p>{profile.bio}</p>
        <div className="d-flex gap-4 mt-2">
          <div style={{ cursor: 'pointer' }} onClick={() => navigate(`/followers/${profile.userId}`, { state: { data: followers } })}><b>{followersCount}</b> Followers</div>
          <div style={{ cursor: 'pointer' }} onClick={() => navigate(`/following/${profile.userId}`, { state: { data: following } })}><b>{followingCount}</b> Following</div>
          <div><b>{postsCount}</b> Posts</div>
        </div>

        <div className="mt-3 d-flex gap-2">
          {isOwnProfile ? (
            <Button variant="primary" onClick={() => navigate('/editPage', { state: { profile } })}>
              Edit Profile
            </Button>
          ) : (
            <>
              <Button
                variant={isFollowing ? "outline-danger" : "primary"}
                onClick={handleFollowToggle}
              >
                {isFollowing ? "Following" : isFollowBack ? "Follow Back" : "Follow"}
              </Button>
              <Button
                variant="success"
                onClick={() => navigate(`/chatDetailPage/${profile.username}`)}>
                Message
              </Button>
            </>
          )}
        </div>

      </div>
    </div>
  );
};

export default ProfileCard;
