import { useLocation, Link, useNavigate } from 'react-router-dom';
import React, { useState, useEffect } from 'react';
import { House, Search, Bell, Chat, Person, Gear, BoxArrowRight, Plus } from 'react-bootstrap-icons';
import ConfirmationDialog from '../Componets/ConfirmationDialog';

const Sidebar = () => {
  const [showModal, setShowModal] = useState(false);
  const navigate = useNavigate();
  const location = useLocation();
  const currentPath = location.pathname;
  const username=localStorage.getItem('username')

  const isActive = (path) =>
    currentPath === path ? 'nav-link active bg-primary text-white' : 'nav-link text-white';

  const handleLogout = () => {
    localStorage.clear();
    setShowModal(false);
    setTimeout(() => {
      navigate("/");
    }, 100);
  };

  useEffect(() => {
    document.body.style.overflow = showModal ? 'hidden' : 'auto';
  }, [showModal]);

  return (
    <>
      <div className="d-flex flex-column p-3 bg-dark text-white" style={{ height: "100vh", width: "250px" }}>
        <div className="mb-4" style={{ fontSize: "25px", marginLeft: "20px" }}>
          Hii, {username}
        </div>
        <ul className="nav nav-pills flex-column mb-auto">
          <li><Link to="/homepage" className={isActive("/homepage")}><House /> Home</Link></li>
          <li><Link to="/search" className={isActive("/search")}><Search /> Search</Link></li>
          <li><Link to="/notifications" className={isActive("/notifications")}><Bell /> Notifications</Link></li>
          <li><Link to="/message" className={isActive("/message")}><Chat /> Messages</Link></li>
          <li><Link to="/profile" className={isActive("/profile")}><Person /> Profile</Link></li>
          <li><Link to="/create" className={isActive("/create")}><Plus /> Create</Link></li>
          <li><Link to="/setting" className={isActive("/setting")}><Gear /> Settings</Link></li>
          <li>
            <span className="nav-link text-white" style={{ cursor: 'pointer' }} onClick={() => setShowModal(true)}>
              <BoxArrowRight /> Logout
            </span>
          </li>
        </ul>
      </div>

      <ConfirmationDialog
        show={showModal}
        title="Confirm Logout"
        message="Are you sure you want to logout?"
        confirmLabel="Logout"
        cancelLabel="Cancel"
        onCancel={() => setShowModal(false)}
        onConfirm={handleLogout}
      />
    </>
  );
};

export default Sidebar;
