import { useEffect, useState } from "react";
import { getFollowing } from "../Service/api";
import { useParams } from 'react-router-dom';
import UserCard from "../FixedComponet/UserCard";
import { useLocation } from "react-router-dom";
import Sidebar from "../FixedComponet/Sidebar";
const FollowingPage = () => {
  const location = useLocation();
  const following= location.state?.data || [];
  return (
    <div className="d-flex">
      <div style={{width:"250px", padding:"10px"}}>
      <Sidebar/>
      </div>
      
    <div className="container mt-4" style={{marginLeft:"30px",marginRight:"30px"}}>
      <h2>Follower</h2>
      {following.length > 0 ? (
        following.map(user => (
          <UserCard key={user.userId} user={user} />
        ))
      ) : (
        <p>No Follower</p>
      )}
    </div>
    </div>
  );
};


export default FollowingPage;
