// src/pages/MessagesPage.js
import React, { useEffect, useState } from "react";
import axios from "axios";
import UserCard from "../FixedComponet/UserCard";
import { useNavigate } from "react-router-dom";
import { Display } from "react-bootstrap-icons";
import Sidebar from "../FixedComponet/Sidebar";

const MessagesPage = () => {
  const [users, setUsers] = useState([]);
  const navigate = useNavigate();
  const username=localStorage.getItem('username');

  useEffect(() => {
    const fetchUsers = async () => {
      try {
        const res = await axios.get(`http://localhost:8080/chat/getChattedUsers/${username}`);
        console.log(res.data);  // Check the response in the console
        setUsers(res.data);
        console.log(res.data)
      } catch (error) {
        console.error("Error fetching users:", error);
      }
    };
    fetchUsers();
  }, []);

  const handleMessageClick = (user) => {
    navigate(`/chatDetailPage/${user.username}`, {
      state: { receiverId: user.userId },
    });
  };

  return (
    <div style={{ display: "flex", height: "100vh" }}>
      {/* Sidebar */}
      <div style={{ width: '250px', position: 'sticky', top: 0, backgroundColor: '#f8f9fa', padding: '10px' }}>
        <Sidebar />
      </div>
  
      {/* Main Content */}
      <div style={{ flex: 1, padding: '20px', marginLeft: '20px' }}>
        <h3 style={{ marginBottom: '20px' }}>Messages</h3>
        {users.map((user) => (
          <UserCard
            key={user.userId}
            user={user}
            type="message"
          />
        ))}
      </div>
    </div>
  );
        }  

export default MessagesPage;
