import { useEffect, useState } from "react";
import axios from "axios";
import UserCard from "../FixedComponet/UserCard";
import { getProfile } from "../Service/api";
import Sidebar from "../FixedComponet/Sidebar";
const NotificationPage = () => {
  const [followRequests, setFollowRequests] = useState([]);
  const [followBackSuggestions, setFollowBackSuggestions] = useState([]);
  const [loggedInUsername, setLoggedInUsername] = useState(localStorage.getItem('username'));
  const [profile, setProfile] = useState([]);
  
  useEffect(() => {
    
    const fetchNotifications = async () => {
      try {
        const profile = await getProfile(loggedInUsername);
        setProfile(profile);
        const userId = profile.userId;

        const requestRes = await axios.get(`http://localhost:8080/profile/follow/requests?userId=${userId}`);
        setFollowRequests(requestRes.data);
        console.log(requestRes.data)
        
        const followBackRes = await axios.get(`http://localhost:8080/profile/followBackSuggestions/${userId}`);
        setFollowBackSuggestions(followBackRes.data ||[]);

      } catch (err) {
        console.error("Error fetching notifications", err);
      }
    };

    fetchNotifications();
  }, [loggedInUsername]);

  const handleConfirmFollow = async (userId) => {
    try {
      await axios.post(`http://localhost:8080/profile/accept/${userId}/${profile.userId}`)
      setFollowRequests(prev => prev.filter(u => u.userId !== userId));
    } catch (err) {
      console.error("Failed to confirm follow", err);
    }
  };

  const handleFollowBack = async (userId) => {
    try {
      await axios.post(`http://localhost:8080/profile/${userId}/${profile.userId}`);
      setFollowBackSuggestions(prev => prev.filter(u => u.userId !== userId));
    } catch (err) {
      console.error("Failed to follow back", err);
    }
  };
  const handleCancelFollow = async (userId) => {
    try {
      await axios.delete(`http://localhost:8080/profile/cancel/${userId}/${profile.userId}`);
      setFollowRequests(prev => prev.filter(u => u.userId !== userId));
    } catch (err) {
      console.error("Failed to cancel follow request", err);
    }
  };

  return (
    <div style={{ display: 'flex', minHeight: '100vh' }}>
      {/* Sidebar */}
      <div
        style={{
          width: '250px',
          height: '100vh',
          position: 'sticky',
          top: 0,
          backgroundColor: '#f8f9fa',
          padding: '10px',
          flexShrink: 0
        }}
      >
        <Sidebar />
      </div>
  
      {/* Main Content */}
      <div className="container mt-4" style={{ flex: 1, padding: '20px' ,marginLeft:"30px"}}>
        <h3>Notifications</h3>
  
        <div className="mt-4">
          <h5>Follow Requests</h5>
          {followRequests.length > 0 ? (
            followRequests.map(user => (
              <UserCard
                key={user.userId}
                user={user}
                type="follow-request"
                onCancel={handleCancelFollow}
                onConfirm={handleConfirmFollow}
              />
            ))
          ) : (
            <p>No follow requests</p>
          )}
        </div>
  
        <div className="mt-4">
          <h5>Follow Back Suggestions</h5>
          {Array.isArray(followBackSuggestions) && followBackSuggestions.length > 0 ? (
            followBackSuggestions.map(user => (
              <UserCard
                key={user.userId}
                user={user}
                type="follow-back"
                onFollowBack={handleFollowBack}
              />
            ))
          ) : (
            <p>No one to follow back</p>
          )}
        </div>
      </div>
    </div>
  );
}  

export default NotificationPage;
