import { useEffect, useState } from "react";
import { getFollowers } from "../Service/api";
import UserCard from "../FixedComponet/UserCard";
import { useParams } from 'react-router-dom';
import { useLocation } from "react-router-dom";
import Sidebar from "../FixedComponet/Sidebar";

const FollowersPage = () => {
  const location = useLocation();
  const follower = location.state?.data || [];

  return (
    <div className="d-flex">
      {/* Sidebar */}
      <div style={{ width: "250px" ,padding:"10px"}}>
        <Sidebar />
      </div>
      {/* Main Content */}
      <div className="container mt-4" style={{ marginLeft: "30px", flex: 1 ,marginRight:"30px"}}>
        <h2>Followers</h2>
        {follower.length > 0 ? (
          follower.map(user => (
            <UserCard key={user.userId} user={user} />
          ))
        ) : (
          <p>No Followers</p>
        )}
      </div>
    </div>
  );
};


export default FollowersPage;
